<template>
	<div class="hold">
		<router-link to="/power">
			<div>
				<img v-lazy="require('../assets/pos.png')"/>
				<div>
					<span>{{ $t('message.hold_1') }}</span>
					<span>{{ hold }}</span>
				</div>
			</div>
			<img v-lazy="require('../assets/pos_next.png')"/>
		</router-link>
		<div class="content">
			<div class="header">
				<div>
					<span>{{ $t('message.hold_2') }}</span>
					<span>{{ $t('message.hold_3') }}</span>
				</div>
				<div>
					<span>{{ selfHold }} UPC</span>
					<span>{{ optimal }} UPC</span>
				</div>
				<div class="progress">
					<div :style="{ width: width }"></div>
				</div>
			</div>
			<div class="input">
				<span>{{ $t('message.hold_4') }}</span>
				<div>
					<input type="number" inputmode="decimal" :placeholder="$t('message.hold_5')" v-model="joinNum">
					<Button @click="joinNum = (optimal - selfHold)">{{ $t('message.hold_6') }}</Button>
				</div>
				<span>{{ $t('message.hold_7') }} {{ balance }} UPC</span>
			</div>
			<div class="submit">
				<!-- <div class="checkbox" @click="checked = !checked">
					<icon name="success" :class="checked ? 'active' : ''"></icon>
					<span><span @click.stop="rule"></span></span>
				</div> -->
				<Button :disabled="isBroken" @click="authorize">{{ $t('message.hold_8') }}</Button>
			</div>
		</div>
		<div class="footer">
			<span>{{ $t('message.hold_9') }}</span>
			<self-histogram
			  ref="echart"
			></self-histogram>
		</div>
		<self-code ref="code" @bindSuccess="bindSuccess"></self-code>
		<self-authorize
		  ref="authorize"
		  :joinNum="joinNum"
		  :balance="balance"
		  @join="join"
		></self-authorize>
	</div>
</template>

<script>
	import { Button, Icon, Toast } from 'vant'
	import selfCode from '@/components/self-code'
	import selfAuthorize from '@/components/self-authorize'
	import selfHistogram from '@/components/self-histogram'
	
	export default {
		components: { Button, Icon, selfCode, selfAuthorize, selfHistogram },
		props: {
			hold: {
				type: [Number, String],
				default: '0.00'
			},
			selfHold: {
				type: [Number, String],
				default: '0.00'
			},
			balance: {
				type: [Number, String],
				default: '0.00'
			},
			code: {
				type: [Number, String],
				default: 0
			}
		},
		data() {
			return {
				// checked: false,
				joinNum: '',
				optimal: '0.00',
				width: 0,
				isBroken: true
			}
		},
		methods: {
			init() {
				if (this.$cache.hold) {
					this.optimal = this.$cache.hold.optimal
					this.width = this.$cache.hold.width
					this.isBroken = this.$cache.hold.isBroken
				} else {
					this.$cache.hold = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.depositToken.methods._KContractBroken()
				.call((err, res) => {
					if (!err) {
						this.isBroken = res
						this.$cache.hold.isBroken = res
					}
				})
				
				this.$contract.depositToken.methods.DepositDistributionInfo(1, 200)
				.call((err, res) => {
					if (!err) {
						let orderNum = 1,
							totalStaticPower = 0,
							quickValues = [],
							bestSpaceID = 0,
							bestSpaceProfixRate = 0
						
						for (let i = 0; i < Number(res.len); i++) {
							quickValues.push(orderNum)
							totalStaticPower += Number(res.spaceSums[i]) * orderNum
							orderNum += Number(res.spaceSums[i])
						}
						
						for (let i = 0; i < Number(res.len); i++) {
							let p = quickValues[i] / totalStaticPower
							if (p > bestSpaceProfixRate) {
								bestSpaceID = i
								bestSpaceProfixRate = p
							}
						}
						
						this.optimal = (bestSpaceID * 100).toFixed(2)
						this.width = `${this.selfHold / this.optimal * 100}%`
						
						this.$cache.hold.optimal = this.optimal
						this.$cache.hold.width = this.width
					}
				})
			},
			rule() {
				console.log('rule')
			},
			authorize() {
				// if (!this.checked) {
				// 	return Toast.fail('')
				// } else 
				if (this.code === 0) {
					Toast.fail(this.$t('message.hold_10'))
					return this.$refs.code.open()
				} else if (this.joinNum === '') {
					return Toast.fail(this.$t('message.hold_11'))
				} else if (Number(this.joinNum) === 0) {
					return Toast.fail(this.$t('message.hold_12'))
				}
				
				this.$refs.authorize.open()
			},
			join() {
				Toast.loading({
					duration: 0,
					forbidClick: true,
					message: this.$t('message.global_1')
				})
				
				this.$contract.depositToken.methods.DepositAppend(this.$toWei(`${this.joinNum}`)).send({
					from: this.$address
				}).on("receipt", res => {
					this.$emit('getData')
					this.$refs.echart.update()
					this.getData()
					this.joinNum = ''
					// this.checked = false
					Toast.clear()
					Toast.success(this.$t('message.hold_13'))
				}).on('error', err => Toast.clear())
			},
			bindSuccess() {
				this.$emit('getData')
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.footer {
		width: 100%;
		height: 391px;
		padding: 12px 22px;
		position: relative;
		background-color: #1B2C42;
		&>span {
			position: absolute;
			top: 12px;
			left: 22px;
			font-size: 24px;
		}
	}
	.content {
		margin-top: 10px;
		margin-bottom: 4px;
		padding: 16px 28px 30px;
		background-color: #1B2C42;
		&>.submit {
			display: flex;
			align-items: center;
			flex-direction: column;
			&>.checkbox {
				display: flex;
				align-items: center;
				font-size: 24px;
				&>i {
					width: 32px;
					height: 32px;
					line-height: 32px;
					margin-right: 8px;
					color: transparent;
					border-radius: 50%;
					border: 2px solid #1FA36E;
				}
				&>.active {
					color: #fff;
					background-color: #1FA36E;
				}
				&>span>span {
					color: #1FA36E;
				}
			}
			&>button {
				width: 400px;
				height: 90px;
				margin-top: 24px;
				line-height: 90px;
			}
		}
		&>.input {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			font-size: 24px;
			margin-bottom: 60px;
			&>div {
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-top: 16px;
				margin-bottom: 8px;
				&>input {
					width: 476px;
					height: 112px;
					font-size: 36px;
					padding: 0 30px;
					border-radius: 10px;
					border: 1px solid #5E6F90;
					background-color: transparent;
				}
				&>button {
					width: 150px;
					height: 112px;
					line-height: 112px;
					color: #1EA087 !important;
					font-size: 32px !important;
					border-radius: 20px !important;
					background-color: #224360 !important;
					border: 2px solid #F3E844 !important;
				}
			}
		}
		&>.header {
			margin-bottom: 32px;
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				font-size: 32px;
				&:nth-of-type(2) {
					margin-top: 6px;
					margin-bottom: 14px;
				}
			}
			&>.progress {
				width: 100%;
				height: 12px;
				position: relative;
				border-radius: 50px;
				background-color: #516A89;
				&>div {
					height: 20px;
					position: absolute;
					left: 0;
					top: -4px;
					border-radius: 50px;
					transition: width .5s linear;
					background-image: linear-gradient(to right, #21C181, #21C1A5);
				}
			}
		}
	}
	.hold {
		&>a {
			display: flex;
			align-items: center;
			justify-content: space-between;
			background-color: #1B2C42;
			padding: 22px 34px 26px 20px;
			&>div {
				display: flex;
				align-items: center;
				&>img {
					width: 94px;
					height: 94px;
					margin-right: 20px;
				}
				&>div {
					display: flex;
					flex-direction: column;
					align-items: flex-start;
					justify-content: center;
					color: #A1BDE1;
					font-size: 28px;
				}
			}
			&>img {
				width: 16px;
				height: 26px;
			}
		}
	}
</style>
