import Vue from 'vue'
import Web3 from 'web3'
import DepositToken from "./DepositToken.json"
import ProtossToken from "./ProtossToken.json"
import Recommend from "./RecommendSmallTeam.json"
import DepositedPool from "./DepositedPool.json"
import DynamicRelaseManager from "./DynamicRelaseManager.json"
import StaticRelaseManager from "./StaticRelaseManager.json"

let $web3, contract, isWallet = true
if (window.ethereum) {
    $web3 = new Web3(ethereum)
} else if (window.web3) {
    $web3 = new Web3(web3.currentProvider)
} else {
    isWallet = false
}

const chainId = 1
if (isWallet) {
    contract = {
        depositToken: new $web3.eth.Contract(DepositToken.abi, DepositToken.networks[chainId].address),
        protossToken: new $web3.eth.Contract(ProtossToken.abi, ProtossToken.networks[chainId].address),
        recommend: new $web3.eth.Contract(Recommend.abi, Recommend.networks[chainId].address),
        depositedPool: new $web3.eth.Contract(DepositedPool.abi, DepositedPool.networks[chainId].address),
        staticRelaseManager: new $web3.eth.Contract(StaticRelaseManager.abi, StaticRelaseManager.networks[chainId].address),
        dynamicRelaseManager: new $web3.eth.Contract(DynamicRelaseManager.abi, DynamicRelaseManager.networks[chainId].address)
    }
}

const fromWei = (balance, digit) => {
    if (digit) {
        return Number($web3.utils.fromWei(balance, 'ether')).toFixed(digit)
    }
    return Number($web3.utils.fromWei(balance, 'ether'))
}

const toWei = (balance) => {
    return $web3.utils.toWei(balance, 'ether')
}

export { $web3, contract, fromWei, toWei, isWallet }