<template>
	<div class="box">
		<div class="card">
			<div>
				<span>{{ (Number(hold) + Number(spread)).toFixed(2) }}</span>
				<span>{{ $t('message.detail_1') }}</span>
			</div>
			<div>
				<div>
					<span>{{ $t('message.detail_2') }}</span>
					<span>{{ hold }} UPC</span>
				</div>
				<i></i>
				<div>
					<span>{{ $t('message.detail_3') }}</span>
					<span>{{ spread }} UPC</span>
				</div>
			</div>
		</div>
		<div class="nav">
			<Button @click="active = true" :class="active ? 'active' : ''">{{ $t('message.detail_4') }}</Button>
			<Button @click="active = false" :class="active ? '' : 'active'">{{ $t('message.detail_5') }}</Button>
		</div>
		<div class="detail" v-show="active">
			<div v-for="(item, index) in detail" :key="index">
				<div>
					<div>
						<span>{{ $t('message.detail_6') }}</span>
						<span>{{ item.hold }}</span>
					</div>
					<span>{{ item.sum }}</span>
				</div>
				<div>
					<div>
						<span>{{ $t('message.detail_7') }}</span>
						<span>{{ item.spread }}</span>
					</div>
					<span>{{ item.time }}</span>
				</div>
			</div>
		</div>
		<div class="record" v-show="!active">
			<div v-for="(item, index) in record" :key="index">
				<div>
					<span>{{ $t('message.detail_8') }}</span>
					<span>{{ item.time }}</span>
				</div>
				<div>
					<span>{{ item.amount }}</span>
					<img v-lazy="require('../assets/card_next.png')"/>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import { Button } from 'vant'
	
	export default {
		components: { Button },
		data() {
			return {
				active: true,
				hold: '0.00',
				spread: '0.00',
				detail: [
					// { hold: '0.12 UPC', spread: '100.12 UPC', sum: '100.24 UPC', time: '2020/03/23' },
					// { hold: '0.12 UPC', spread: '100.12 UPC', sum: '100.24 UPC', time: '2020/03/23' },
					// { hold: '0.12 UPC', spread: '100.12 UPC', sum: '100.24 UPC', time: '2020/03/23' }
				],
				record: [
					// { amount: '50.00 UPC', time: '2020/03/23 23:21' },
					// { amount: '150.00 UPC', time: '2020/03/23 23:21' },
					// { amount: '50.00 UPC', time: '2020/03/23 23:21' }
				]
			}
		},
		methods: {
			init() {
				if (this.$cache.detail) {
					this.hold = this.$cache.detail.hold
					this.spread = this.$cache.detail.spread
				} else {
					this.$cache.detail = {}
				}
				
				this.getData()
			},
			getData() {
				// this.$contract.depositToken.methods.CurrentRelaseProfixStatic(
				// 	this.$address, parseInt(new Date().getTime() / 1000)
				// ).call((err, hold) => {
				this.$contract.depositToken.methods.CurrentRelaseProfixStatic(
					this.$address, 0
				).call((err, res) => {
					if (!err) {
						console.log(res)
						this.hold = this.$fromWei(res.sum, 2)
						this.$cache.detail.hold = this.hold
					}
				})
				
				// this.$contract.depositToken.methods.CurrentRelaseProfixDynamic(
				// 	this.$address, parseInt(new Date().getTime() / 1000)
				// ).call((err, res) => {
				this.$contract.depositToken.methods.CurrentRelaseProfixDynamic(
					this.$address, 0
				).call((err, res) => {
					if (!err) {
						this.spread = this.$fromWei(res.sum, 2)
						this.$cache.detail.spread = this.spread
					}
				})
				
				this.$http.getHold().then(res => {
					console.log(res)
				})
				
				this.$http.getSpread().then(res => {
					console.log(res)
				})
				
				this.$http.getDraw().then(res => {
					console.log(res)
				})
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.record {
		width: 100%;
		height: calc(100vh - 370px);
		overflow-y: auto;
		&>div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 14px 31px;
			background-color: #1B2C42;
			margin-bottom: 4px;
			&>div {
				display: flex;
				&:first-child {
					flex-direction: column;
					align-items: flex-start;
					font-size: 20px;
					&>span:first-child {
						color: #28AC79;
						margin-bottom: 4px;
					}
				}
				&:last-child {
					align-items: center;
					font-size: 28px;
					&>img {
						width: 9px;
						height: 17px;
						margin-left: 24px;
					}
				}
			}
		}
	}
	.detail {
		width: 100%;
		height: calc(100vh - 370px);
		overflow-y: auto;
		&>div {
			display: flex;
			flex-direction: column;
			font-size: 20px;
			padding: 8px 28px;
			margin-bottom: 10px;
			background-color: #19283E;
			box-shadow: 0px 6px 12px rgba(0, 0, 0, .3);
			&>div {
				display: flex;
				align-items: center;
				justify-content: space-between;
				&>div>span:first-child {
					margin-right: 12px;
				}
				&:first-child {
					margin-bottom: 4px;
				}
			}
		}
	}
	.nav {
		width: calc(100% - 26px);
		height: 80px;
		margin: 14px auto;
		display: flex;
		align-items: center;
		border: 2px solid #455E7C;
		&>button {
			width: 50%;
			height: 100%;
			line-height: 80px;
			color: #fff !important;
			font-size: 32px !important;
			border-radius: 0px !important;
			background-color: #1A2D42 !important;
		}
		&>.active {
			border: 2px solid #D3CE52 !important;
			background-color: #213A54 !important;
		}
	}
	.card {
		width: calc(100% - 26px);
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-radius: 20px;
		padding: 20px 30px 28px 58px;
		background-image: linear-gradient(to right, #228E79, #1C384C);
		&>div {
			display: flex;
			align-items: center;
			flex-direction: column;
			&:first-child {
				color: #fff;
				&>span {
					&:first-child {
						font-size: 40px;
					}
					&:last-child {
						font-size: 28px;
					}
				}
			}
			&:last-child {
				width: 260px;
				&>div {
					width: 100%;
					height: calc(50% - 1px);
					font-size: 28px;
					display: flex;
					flex-direction: column;
					align-items: flex-start;
					padding-left: 40px;
				}
				&>i {
					width: 100%;
					height: 2px;
					margin: 9px 0;
					background-color: #121D2C;
				}
			}
		}
	}
	.box {
		padding: 28px 15px 0;
	}
</style>
