<template>
	<overlay :show="show" class="code" @click="show = false">
		<div @click.stop>
			<span>{{ $t('message.code_1') }}</span>
			<input type="text" autofocus :placeholder="$t('message.code_2')" v-model="otherCode">
			<input type="text" :placeholder="$t('message.code_3')" v-model="selfCode">
		</div>
		<Button @click.stop="bind">{{ $t('message.code_4') }}</Button>
	</overlay>
</template>

<script>
	import { Overlay, Button, Toast } from 'vant'
	
	export default {
		components: { Overlay, Button },
		data() {
			return {
				show: false,
				otherCode: '',
				selfCode: ''
			}
		},
		methods: {
			async bind() {
				if (this.otherCode.length < 6) {
					return Toast.fail(this.$t('message.code_5'))
				} else if (this.selfCode.length !== 6) {
					return Toast.fail(this.$t('message.code_6'))
				}
				this.show = false
				Toast.loading({
					duration: 0,
					forbidClick: true,
					message: this.$t('message.global_1')
				})
				
				let otherCode = this.$web3.utils.stringToHex(this.otherCode.toUpperCase()),
				    selfCode = this.$web3.utils.stringToHex(this.selfCode.toUpperCase()),
				    toAddress = await this.$contract.recommend.methods.ShortCodeToAddress(otherCode).call(),
				    selfAddress = await this.$contract.recommend.methods.ShortCodeToAddress(selfCode).call()
					
				if (!parseInt(toAddress, 16)) {
					Toast.clear()
				    return Toast.fail(this.$t('message.code_7'))
				} else if (parseInt(selfAddress, 16)) {
					Toast.clear()
				    return Toast.fail(this.$t('message.code_8'))
				}
				
				this.$contract.recommend.methods.BindRelationEx(toAddress, selfCode).send({
				    from: this.$address
				}).on("receipt", res => {
					this.otherCode = ''
					this.selfCode = ''
					this.$emit('bindSuccess')
					Toast.clear()
					Toast.success(this.$t('message.code_9'))
				}).on('error', err => Toast.clear())
			},
			open() {
				this.show = true
			}
		}
	}
</script>

<style lang="scss" scoped>
	.code {
		display: flex;
		align-items: center;
		flex-direction: column;
		padding: 210px 69px 0;
		&>div {
			border-radius: 20px;
			padding: 0 30px 60px;
			background-color: #29405D;
			&>span {
				display: block;
				width: 100%;
				color: #fff;
				font-size: 28px;
				padding: 20px 0;
				text-align: left;
				border-bottom: 2px solid #399881;
			}
			&>input {
				border: none;
				width: 100%;
				padding: 20px;
				font-size: 28px;
				border-radius: 10px;
				background-color: #1D2C42;
				&:nth-of-type(1) {
					margin: 20px 0 10px;
				}
			}
		}
		&>button {
			width: 360px;
			height: 80px;
			margin-top: 42px;
			line-height: 80px;
			font-size: 32px !important;
		}
	}
</style>
