<template>
	<div class="histogram">
		<div id="chart"></div>
	</div>
</template>

<script>
	import echarts from 'echarts'
	
	export default {
		data() {
			return {
				echart: ''
			}
		},
		methods: {
			getData() {
				this.$contract.depositToken.methods.DepositDistributionInfo(1, 200)
				.call((err, res) => {
					if (!err) {
						let orderNum = 1,
							totalStaticPower = 0,
							quickValues = [],
							bestSpaceID = 0,
							bestSpaceProfixRate = 0
						
						for (let i = 0; i < Number(res.len); i++) {
							quickValues.push(orderNum)
							totalStaticPower += Number(res.spaceSums[i]) * orderNum
							orderNum += Number(res.spaceSums[i])
						}
						
						for (let i = 0; i < Number(res.len); i++) {
							let p = quickValues[i] / totalStaticPower
							if (p > bestSpaceProfixRate) {
								bestSpaceID = i
								bestSpaceProfixRate = p
							}
						}
						
						let interval = 1000, x1 = 0, x2 = interval, data = [], sum = 0
						res.spaceSums.unshift(0)
						res.spaceSums.forEach((item, index) => {
							sum += Number(item)
							if ((index + 1) % 10 === 0) {
								if (index - 9 <= bestSpaceID && index >= bestSpaceID) {
									data.push({
										value: [x1, x2, sum],
										itemStyle: {
											color: '#279A8A'
										}
									})
								} else {
									data.push({
										value: [x1, x2, sum]
									})
								}
								sum = 0
								x1 = x2
								x2 += interval
							}
						})
						
						function renderItem(params, api) {
						    var yValue = api.value(2);
						    var start = api.coord([api.value(0), yValue]);
						    var size = api.size([api.value(1) - api.value(0), yValue]);
						    var style = api.style();
						
						    return {
						        type: 'rect',
						        shape: {
						            x: start[0] + 1,
						            y: start[1],
						            width: size[0] - 2,
						            height: size[1]
						        },
						        style: style
						    }
						}
						
						this.echart = echarts.init(document.getElementById('chart'))
						this.echart.setOption({
							color: ['#526A89'],
							textStyle: {
								color: '#526A89'
							},
							grid: {
								left: 10,
								right: 20
							},
							xAxis: [{
								type: 'value',
								min: 0,
								max: 20000,
								interval: 1000,
								axisLabel: {
									formatter: (value, index) => {
										if (value > 0) {
											let num = value / 1000
											return num < 20 ? `${num}k` : `${num}k+`
										}
										return value
						    	}
								},
								axisLine: {
									lineStyle: {
										color: '#526A89'
									}
									// show: false
								},
								// axisTick: {
								// 	show: false  
								// },
								splitLine: {
									show: false
								}
							}],
							yAxis: [{
								show: false
							}],
							series: [{
								name: 'height',
								type: 'custom',
								renderItem: renderItem,
								label: {
									show: true,
									position: 'top'
								},
								encode: {
									x: [0, 1],
									y: 2,
									tooltip: 2,
									label: 2
								},
								data: data
							}]
						})
					}
				})
			},
			update() {
				this.$contract.depositToken.methods.DepositDistributionInfo(1, 200)
				.call((err, res) => {
					if (!err) {
						let orderNum = 1,
							totalStaticPower = 0,
							quickValues = [],
							bestSpaceID = 0,
							bestSpaceProfixRate = 0
						
						for (let i = 0; i < Number(res.len); i++) {
							quickValues.push(orderNum)
							totalStaticPower += Number(res.spaceSums[i]) * orderNum
							orderNum += Number(res.spaceSums[i])
						}
						
						for (let i = 0; i < Number(res.len); i++) {
							let p = quickValues[i] / totalStaticPower
							if (p > bestSpaceProfixRate) {
								bestSpaceID = i
								bestSpaceProfixRate = p
							}
						}
						
						let interval = 1000, x1 = 0, x2 = interval, data = [], sum = 0
						res.spaceSums.unshift(0)
						res.spaceSums.forEach((item, index) => {
							sum += Number(item)
							if ((index + 1) % 10 === 0) {
								if (index - 9 <= bestSpaceID && index >= bestSpaceID) {
									data.push({
										value: [x1, x2, sum],
										itemStyle: {
											color: '#279A8A'
										}
									})
								} else {
									data.push({
										value: [x1, x2, sum]
									})
								}
								sum = 0
								x1 = x2
								x2 += interval
							}
						})
						
						this.echart.setOption({
							series: [{
								data: data
							}]
						})
					}
				})
			}
		},
		created() {
			this.getData()
		}
	}
</script>

<style lang="scss" scoped>
	.histogram {
		width: 100%;
		height: 100%;
		overflow-x: auto;
		&>#chart {
		    width: 1200px;
		    height: 100%;
		}
	}
</style>
