import axios from 'axios'
import { $web3, contract } from './static/initData'

const http = {
	getJoin: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.depositedPool._address,
				sha3('LogDepositedChanged(address,int,uint)')
			).then(resolve).catch(reject)
		})
	},
	getDraw: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.depositToken._address,
				sha3('LogRelaseDay(address,uint,uint)')
			).then(resolve).catch(reject)
		})
	},
	getHold: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.staticRelaseManager._address,
				sha3('LogStaticRelaseDay(address,uint,uint)')
			).then(resolve).catch(reject)
		})
	},
	getSpread: () => {
		return new Promise((resolve, reject) => {
			send(
				contract.dynamicRelaseManager._address,
				sha3('LogDynamicRelaseDay(address,uint,uint)')
			).then(resolve).catch(reject)
		})
	}
}

function sha3(str) {
	return $web3.utils.sha3(str)
}

function send(address, method) {
	return new Promise((resolve, reject) => {
		axios.get('https://api-cn.etherscan.com/api',{
			params: {
				"module": 'logs',
				"action": 'getLogs',
				"fromBlock": 0,
				"toBlock": 'latest',
				"address": address,
				"topic0": method,
				"apikey":'APJQ2KZQX7GQMD9FTUK986WSBRVEB7TUYU'
			}
		}).then(res => {
			resolve(res.data.result)
		}).catch(reject)
	})
}

export default http