<script>
	import { Button, Overlay, Toast } from 'vant'
	
	export default {
		components: { Button, Overlay },
		data() {
			return {
				hold: '0.00',
				total: 0,
				less: false,
				lessNum: '',
				record: []
			}
		},
		methods: {
			init() {
				if (this.$cache.power) {
					this.hold = this.$cache.power.hold
					this.total = this.$cache.power.total
				} else {
					this.$cache.power = {}
				}
				
				this.getData()
			},
			getData() {
				// this.$contract.depositToken.methods.DepositedInfo(
				// 	this.$address, parseInt(new Date().getTime() / 1000)
				// ).call((err, res) => {
				this.$contract.depositToken.methods.DepositedInfo(
					this.$address, 0
				).call((err, res) => {
					if (!err) {
						this.total = this.$fromWei(res.depositedAmount)
						this.hold = this.total.toFixed(2)
						
						this.$cache.power.total = this.total
						this.$cache.power.hold = this.hold
					}
				})
				
				// this.$contract.depositedPool.events.LogDeposited({
				// 	filter: { owner: this.$address },
				// 	fromBlock: 0
				// }).on('data', res => {
				// 	let amount = this.$fromWei(res.returnValues.amount, 2),
				// 		date = new Date(res.returnValues.time * 1000),
				// 		year = date.getFullYear(),
				// 		month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
				// 		day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate(),
				// 		hour = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours(),
				// 		minute = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
						
				// 	if (amount > 0) {
				// 		this.record.unshift({
				// 			type: this.$t('message.power_18'),
				// 			amount: `+${amount} UPC`,
				// 			time: `${year}/${month}/${day} ${hour}:${minute}`
				// 		})
				// 	} else {
				// 		this.record.unshift({
				// 			type: this.$t('message.power_19'),
				// 			amount: `-${amount} UPC`,
				// 			time: `${year}/${month}/${day} ${hour}:${minute}`
				// 		})
				// 	}
				// })
				this.$http.getJoin().then(res => {
					console.log(res)
				})
			},
			lessHold() {
				if (this.lessNum === '') {
					return Toast.fail(this.$t('message.power_20'))
				} else if (Number(this.lessNum) === 0) {
					return Toast.fail(this.$t('message.power_21'))
				} else if (Number(this.lessNum) > Number(this.hold)) {
					return Toast.fail(this.$t('message.power_22'))
				}
				
				this.less = false
				Toast.loading({
					duration: 0,
					forbidClick: true,
					message: this.$t('message.global_1')
				})
				
				this.$contract.depositToken.methods.DepositDivestment(this.$toWei(`${this.lessNum}`)).send({
					from: this.$address,
				}).on("receipt", res => {
					this.getData()
					this.lessNum = ''
					Toast.clear()
					Toast.success(this.$t('message.power_23'))
				}).on('error', err => Toast.clear())
			},
			open() {
				if (Number(this.hold) === 0) {
					return Toast.fail(this.$t('message.power_24'))
				}
				this.less = true
			}
		},
		mounted() {
			this.init()
		}
	}
</script>

