<template>
	<overlay :show="show" class="authorize" @click="show = false">
		<div @click.stop>
			<img v-lazy="require('../assets/authorize.png')"/>
			<div class="title">
				<span>{{ $t('message.authorize_1') }}</span>
				<span>2020/03/23  01.12</span>
			</div>
			<div class="prompt">
				<i></i>
				<span>{{ $t('message.authorize_2') }}</span>
			</div>
			<div class="input">
				<span>{{ $t('message.authorize_3') }}: {{ joinNum }} UPC</span>
				<div>
					<input autofocus type="number" inputmode="numeric" v-model="authorizeNum">
					<div>
						<img @click="plus" v-lazy="require('../assets/plus.png')"/>
						<img @click="less" v-lazy="require('../assets/less.png')"/>
					</div>
					<span>UPC</span>
				</div>
				<!-- <span>{{ available }} UPC</span> -->
			</div>
			<Button @click="authorize">{{ $t('message.authorize_4') }}</Button>
		</div>
	</overlay>
</template>

<script>
	import { Overlay, Toast } from 'vant'
	
	export default {
		components: { Overlay },
		props: {
			joinNum: [Number, String],
			balance: [Number, String]
		},
		data() {
			return {
				show: false,
				authorizeNum: '',
				available: '0.00'
			}
		},
		methods: {
			authorize() {
				if (Number(this.authorizeNum) !== Number(this.joinNum)) {
					return Toast.fail(`${this.$t('message.authorize_5')}${this.joinNum}`)
				}
				
				this.show = false
				Toast.loading({
					duration: 0,
					forbidClick: true,
					message: this.$t('message.global_1')
				})
				
				this.$contract.protossToken.methods.approve(this.$contract.depositToken._address, this.$toWei(`${this.authorizeNum}`)).send({
					from: this.$address
				}).on("receipt", res => {
					this.$contract.protossToken.methods.allowance(this.$address, this.$contract.depositToken._address)
					.call((error, result) => {
						if (!error) {
							if (this.$fromWei(result) >= Number(this.joinNum)) {
								Toast.clear()
								Toast.success(this.$t('message.authorize_6'))
								setTimeout(() => { this.$emit('join') }, 1000)
							}
						} else {
							Toast.clear()
							Toast.fail(this.$t('message.authorize_7'))
						}
					})
				}).on('error', err => Toast.clear())
			},
			open() {
				this.show = true
				this.authorizeNum = this.joinNum
			},
			plus() {
				let num = Number(this.authorizeNum)
				if (num + 100 > Number(this.balance)) {
					num = parseInt(this.balance)
				} else {
					num += 100
				}
				this.authorizeNum = num
			},
			less() {
				let num = Number(this.authorizeNum)
				if (num - 100 < 0) {
					num = 0
				} else {
					num -= 100
				}
				this.authorizeNum = num
			}
		},
		// mounted() {
		// 	this.$contract.protossToken.methods.allowance(this.$address, this.$contract.depositToken._address)
		// 	.call((err, res) => {
		// 		if (!err) {
		// 			this.available = this.$fromWei(res, 2)
		// 		}
		// 	})
		// }
	}
</script>

<style lang="scss" scoped>
	.authorize {
		padding: 48px 30px 0;
		&>div {
			width: 100%;
			border-radius: 10px;
			padding: 40px 44px 62px;
			background-color: #2A405C;
			&>button {
				width: 360px;
				height: 80px;
				margin-top: 42px;
				line-height: 80px;
				font-size: 32px !important;
			}
			&>.input {
				width: 100%;
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				&>span {
					font-size: 28px;
					&:first-child {
						color: #21C19E;
					}
				}
				&>div {
					display: flex;
					align-items: center;
					margin: 14px 0;
					font-size: 40px;
					border-radius: 10px;
					background-color: #1A2B43;
					padding: 0 40px 0 22px;
					&>input {
						width: 100%;
						border: none;
						font-size: 36px;
						padding-right: 20px;
						background-color: transparent;
					}
					&>div {
						display: flex;
						align-items: center;
						flex-direction: column;
						margin-right: 58px;
						padding: 11px 0;
						padding-right: 30px;
						border-right: 1px solid #55688B;
						&>img {
							width: 21px;
							height: 12px;
							&:first-child {
								margin-bottom: 40px;
							}
						}
					}
				}
			}
			&>.prompt {
				margin: 39px 0;
				display: flex;
				align-items: flex-start;
				font-size: 28px;
				text-align: left;
				&>i {
					width: 6px;
					height: 32px;
					margin-right: 22px;
					background-color: #258879;
				}
			}
			&>img {
				width: 99px;
				height: 112px;
			}
			&>.title {
				width: 100%;
				display: flex;
				align-items: center;
				flex-direction: column;
				padding: 14px 0 26px;
				border-bottom: 2px solid #0E2842;
				&>span {
					&:first-child {
						font-size: 32px;
						margin-bottom: 4px;
					}
					&:last-child {
						font-size: 24px;
					}
				}
			}
		}
	}
</style>
