<template>
	<div class="box">
		<div class="card">
			<div class="logo">
				<img v-lazy="require('../assets/card_logo.png')"/>
				<router-link to="/detail">
					<span>{{ $t('message.home_1') }}</span>
					<img v-lazy="require('../assets/card_next.png')"/>
					<img v-lazy="require('../assets/card_next.png')"/>
				</router-link>
			</div>
			<div class="income">
				<span>{{ income }}</span>
				<Button @click="draw">{{ $t('message.home_2') }}</Button>
			</div>
			<div class="address">
				<span>{{ `${$address.slice(0, 6)}****${$address.slice(-6)}` }}</span>
				<span>{{ balance }}UPC</span>
			</div>
			<div class="amount">
				<div>
					<span>{{ hold }}</span>
					<span>{{ $t('message.index_3') }}</span>
				</div>
				<i></i>
				<div>
					<span>{{ spread }}</span>
					<span>{{ $t('message.index_4') }}</span>
				</div>
			</div>
		</div>
		<div class="navbar">
			<Button @click="active = true" :class="active ? 'active' : ''">{{ $t('message.home_3') }}</Button>
			<Button @click="active = false" :class="active ? '' : 'active'">{{ $t('message.home_4') }}</Button>
		</div>
		<self-hold 
		  v-show="active"
		  :hold="hold"
		  :self-hold="selfHold"
		  :balance="balance"
		  :code="code"
		  @getData="getData"
		></self-hold>
		<self-spread
		  v-show="!active"
		  :code="code"
		  @getData="getData"
		></self-spread>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	import selfHold from '@/components/self-hold'
	import selfSpread from '@/components/self-spread'
	
	export default {
		components: { Button, selfHold, selfSpread },
		data() {
			return {
				active: true,
				income: '0.00',
				balance: '0.00',
				hold: '0.00',
				spread: '0.00',
				selfHold: '0.00',
				code: 0,
			}
		},
		methods: {
			init() {
				if (this.$cache.home) {
					this.income = this.$cache.home.income
					this.balance = this.$cache.home.balance
					this.hold = this.$cache.home.hold
					this.spread = this.$cache.home.spread
					this.selfHold = this.$cache.home.selfHold
					this.code = this.$cache.home.code
				} else {
					this.$cache.home = {}
				}
				
				this.getData()
			},
			getData() {
				// this.$contract.depositToken.methods.CurrentRelaseProfixStatic(
				// 	this.$address, parseInt(new Date().getTime() / 1000)
				// ).call((err, hold) => {
				this.$contract.depositToken.methods.CurrentRelaseProfixStatic(
					this.$address, 0
				).call((err, hold) => {
					if (!err) {
						
						// this.$contract.depositToken.methods.CurrentRelaseProfixDynamic(
						// 	this.$address, parseInt(new Date().getTime() / 1000)
						// ).call((error, spread) => {
						this.$contract.depositToken.methods.CurrentRelaseProfixDynamic(
							this.$address, 0
						).call((error, spread) => {
							if (!error) {
								this.income = (this.$fromWei(hold.sum) + this.$fromWei(spread.sum)).toFixed(2)
								this.$cache.home.income = this.income
								
								// console.log(`: ${this.$fromWei(hold.sum, 2)}UPC`);
								// console.log(`: ${this.$fromWei(spread.sum, 2)}UPC`);
							} else {
								this.income = this.$fromWei(hold.sum, 2)
								this.$cache.home.income = this.income
							}
						})
						
					} else {
						this.$cache.home.income = '0.00'
					}
				})
				
				this.$contract.protossToken.methods.balanceOf(this.$address)
				.call((err, res) => {
					if (!err) {
						this.balance = this.$fromWei(res, 2)
						this.$cache.home.balance = this.balance
					}
				})
				
				// this.$contract.depositToken.methods.DepositedInfo(
				// 	this.$address, parseInt((new Date().getTime() - 60000) / 1000)
				// ).call((err, res) => {
				this.$contract.depositToken.methods.DepositedInfo(
					this.$address, 0
				).call((err, res) => {
					if (!err) {
						this.hold = res.staticValue
						this.spread = res.dynamicValue
						this.selfHold = this.$fromWei(res.depositedAmount, 2)
						
						this.$cache.home.hold = this.hold
						this.$cache.home.spread = this.spread
						this.$cache.home.selfHold = this.selfHold
						
						if (Number(res.shortCode) != 0) {
							this.code = this.$web3.utils.hexToString(res.shortCode)
						} else {
							this.code = 0
						}
						this.$cache.home.code = this.code
					}
				})
			},
			draw() {
				if (Number(this.income) === 0) {
					return Toast.fail(this.$t('message.home_5'))
				}
				Toast.loading({
					duration: 0,
					forbidClick: true,
					message: this.$t('message.global_1')
				})
				
				// this.$contract.depositToken.methods.WithdrawProfix(parseInt(new Date().getTime() / 1000)).send({
				this.$contract.depositToken.methods.WithdrawProfix(0).send({
					from: this.$address
				}).on("receipt", res => {
					this.getData()
					Toast.clear()
					Toast.success(this.$t('message.home_6'))
				}).on('error', err => Toast.clear())
			}
		},
		async mounted() {
			this.init()
			
			// let info = await this.$web3.eth.getBlock('latest').then(),
			// 	date = new Date(info.timestamp * 1000),
			// 	year = date.getFullYear(),
			// 	month = date.getMonth() + 1,
			// 	day = date.getDate(),
			// 	hour = date.getHours(),
			// 	minute = date.getMinutes(),
			// 	second = date.getSeconds()
			
			// console.log(`${year}/${month}/${day} ${hour}:${minute}:${second}`)
		}
	}
</script>

<style lang="scss" scoped>
	.navbar {
		width: 100%;
		height: 80px;
		display: flex;
		align-items: center;
		border: 2px solid #455E7C;
		margin-top: 20px;
		margin-bottom: 10px;
		&>button {
			width: 50%;
			height: 100%;
			line-height: 80px;
			color: #fff !important;
			font-size: 32px !important;
			border-radius: 0px !important;
			background-color: #1A2D42 !important;
		}
		&>.active {
			border: 2px solid #D3CE52 !important;
			background-color: #213A54 !important;
		}
	}
	.card {
		width: 100%;
		border-radius: 20px;
		padding: 24px 0 28px;
		background-image: linear-gradient(to right, #228B7A, #1E4A52);
		&>.amount {
			display: flex;
			align-items: center;
			justify-content: space-between;
			color: #fff;
			&>div {
				width: calc(50% - 1px);
				display: flex;
				align-items: center;
				flex-direction: column;
				&>span {
					&:first-child {
						font-size: 36px;
					}
					&:last-child {
						font-size: 24px;
					}
				}
			}
			&>i {
				width: 2px;
				height: 76px;
				background-color: #279F74;
			}
		}
		&>.address {
			display: flex;
			align-items: center;
			justify-content: space-between;
			color: #809BA0;
			font-size: 28px;
			padding: 8px 42px;
			background-color: #193744;
			margin-top: 20px;
			margin-bottom: 26px;
		}
		&>.income {
			display: flex;
			align-items: center;
			flex-direction: column;
			&>span {
				color: #fff;
				font-size: 60px;
			}
			&>button {
				height: 40px;
				margin-top: 10px;
				line-height: 40px;
				color: #EBF3F2 !important;
				font-size: 24px !important;
				border: 2px solid #F3E844 !important;
				background-color: transparent !important;
			}
		}
		&>.logo {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-left: 26px;
			padding-right: 40px;
			&>img {
				width: 36px;
				height: 44px;
			}
			&>a {
				display: flex;
				align-items: center;
				color: rgba(255, 255, 255, .5);
				font-size: 40px;
				&>span {
					line-height: 1;
					margin-right: -30px;
					transform: scale(.5);
				}
				&>img {
					width: 9px;
					height: 17px;
					&:last-child {
						margin-left: -3px;
					}
				}
			}
		}
	}
	.box {
		padding: 30px 28px 0;
	}
</style>
