<template>
	<div class="spread">
		<div class="content" v-if="code === 0">
			<div>
				<div>
					<img v-lazy="require('../assets/code.png')"/>
					<div>
						<span>{{ $t('message.spread_1') }}</span>
						<span>******</span>
					</div>
				</div>
				<div @click="open">
					<span>{{ $t('message.spread_2') }}</span>
					<img v-lazy="require('../assets/pos_next.png')"/>
				</div>
			</div>
			<div>
				<div>
					<img v-lazy="require('../assets/link.png')"/>
					<div>
						<span>{{ $t('message.spread_3') }}</span>
						<span>{{ link }}******</span>
					</div>
				</div>
				<div @click="open">
					<span>{{ $t('message.spread_2') }}</span>
					<img v-lazy="require('../assets/pos_next.png')"/>
				</div>
			</div>
		</div>
		<div class="content" v-else>
			<div>
				<div>
					<img v-lazy="require('../assets/code.png')"/>
					<div>
						<span>{{ $t('message.spread_1') }}</span>
						<span>{{ code }}</span>
					</div>
				</div>
				<div
				  v-clipboard:copy="code"
				  v-clipboard:success="onCopySuccess"
				  v-clipboard:error="onCopyError"
				>
					<span>{{ $t('message.spread_4') }}</span>
					<img v-lazy="require('../assets/pos_next.png')"/>
				</div>
			</div>
			<div>
				<div>
					<img v-lazy="require('../assets/link.png')"/>
					<div>
						<span>{{ $t('message.spread_3') }}</span>
						<span>{{ `${link}?code=${code}` }}</span>
					</div>
				</div>
				<div
				  v-clipboard:copy="`${link}?code=${code}`"
				  v-clipboard:success="onCopySuccess"
				  v-clipboard:error="onCopyError"
				>
					<span>{{ $t('message.spread_4') }}</span>
					<img v-lazy="require('../assets/pos_next.png')"/>
				</div>
			</div>
		</div>
		<div class="list">
			<span>{{ $t('message.spread_5') }}</span>
			<div class="nav">
				<span>{{ $t('message.spread_6') }}</span>
				<span>{{ $t('message.spread_7') }}</span>
				<span>{{ $t('message.spread_8') }}</span>
				<span>{{ $t('message.spread_9') }}</span>
				<span>{{ $t('message.spread_10') }}</span>
			</div>
			<div class="record">
				<div v-for="(item, index) in record" :key="index">
					<span>{{ item.id }}</span>
					<span>{{ item.amount }}</span>
					<span>{{ item.hold }}</span>
					<span>{{ item.node }}</span>
					<span>{{ item.spread }}</span>
				</div>
			</div>
		</div>
		<self-code ref="code" @bindSuccess="bindSuccess"></self-code>
	</div>
</template>

<script>
	import { Toast } from 'vant'
	import selfCode from '@/components/self-code'
	
	export default {
		components: { selfCode },
		props: {
			code: {
				type: [Number, String],
				default: 0
			}
		},
		data() {
			return {
				record: [],
				link: ''
			}
		},
		methods: {
			init() {
				if (this.$cache.spread) {
					this.record = this.$cache.spread.record
				} else {
					this.$cache.spread = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.recommend.methods.RecommendList(this.$address, 0)
				.call(async (err, res) => {
					if (!err) {
						let list = []
						
						for (let i = 0; i < res.list.length; i++) {
							// let result = await this.$contract.depositToken.methods.DepositedInfo(
							// 	res.list[i], parseInt(new Date().getTime() / 1000)
							// ).call(),
							let result = await this.$contract.depositToken.methods.DepositedInfo(res.list[i], 0).call(),
								node = await this.$contract.recommend.methods.TeamMemberTotal(res.list[i]).call()
							
							list.push({
								id: this.$web3.utils.hexToString(result.shortCode),
								amount: this.$fromWei(result.depositedAmount, 2),
								hold: result.staticValue,
								node: node,
								spread: result.dynamicValue
							})
						}
						
						this.record = list
						this.$cache.spread.record = list
					}
				})
			},
			open() {
				this.$refs.code.open()
			},
			onCopySuccess() {
			    Toast.success(this.$t('message.spread_11'))
			},
			onCopyError() {
			    Toast.fail(this.$t('message.spread_12'))
			},
			bindSuccess() {
				this.$emit('getData')
			}
		},
		mounted() {
			this.link = location.href.slice(0, location.href.indexOf('home'))
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.list {
		width: 100%;
		height: 520px;
		padding: 22px 19px;
		background-color: #1B2C42;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		&>.record {
			width: 100%;
			height: calc(100% - 110px);
			overflow-y: auto;
			&>div {
				width: 100%;
				font-size: 40px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				&>span {
					display: inline-block;
					width: calc(100% / 5);
					font-size: 20px;
					text-align: center;
					&:first-child {
						text-align: left;
					}
					&:last-child {
						text-align: right;
					}
				}
			}
		}
		&>.nav {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-top: 2px solid #17253A;
			border-bottom: 2px solid #17253A;
			margin-top: 22px;
			&>span {
				display: inline-block;
				width: calc(100% / 5);
				font-size: 20px;
				text-align: center;
				padding: 10px 0;
				&:first-child {
					text-align: left;
				}
				&:last-child {
					text-align: right;
				}
			}
		}
		&>span {
			font-size: 28px;
			font-weight: 400;
		}
	}
	.content {
		margin: 14px 0;
		&>div {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 23px 21px;
			background-color: #1B2C42;
			&>div {
				display: flex;
				align-items: center;
				font-size: 32px;
				&:first-child {
					&>img {
						width: 88px;
						height: 88px;
						margin-right: 20px;
					}
					&>div {
						display: flex;
						flex-direction: column;
						align-items: flex-start;
						&>span:first-child {
							margin-bottom: 2px;
						}
						&>span:last-child {
							width: 420px;
							text-align: left;
							display: inline-block;
							word-wrap: break-word;
						}
					}
				}
				&:last-child {
					&>img {
						width: 16px;
						height: 26px;
						margin-left: 10px;
					}
				}
			}
			&:last-child {
				margin-top: 14px;
			}
		}
	}
</style>
